@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: 'Ubuntu', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-webkit-keyframes fadeIn {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}

@-webkit-keyframes fromTop {
  0% {
    -webkit-transform: translateY(-30%);
            transform: translateY(-30%);
  }
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}

@keyframes fromTop {
  0% {
    -webkit-transform: translateY(-30%);
            transform: translateY(-30%);
  }
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}

@-webkit-keyframes fromBottom {
  0% {
    -webkit-transform: translateY(30%);
            transform: translateY(30%);
  }
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}

@keyframes fromBottom {
  0% {
    -webkit-transform: translateY(30%);
            transform: translateY(30%);
  }
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}
.text {
    -webkit-transition: color 0.5s;
    transition: color 0.5s;
}

#body {
    min-width: 100vw;
    min-height: 100vh;
    min-height: -webkit-fill-available;
    /* transform: scale(0.7); */

    display: flex;
    align-items: center;
}

/* ! --------- ! */
/* * DARK MODE * */
/* ! --------- ! */

#body.light {
    background: url('/background2.png'); 
    background-size: cover;
    color: #171710;
}

#body.light svg {
    fill: #171710;
}

#body.light #background-color-fader {
    background-color: #fff;
    opacity: 0.3;
}

#body.light #period {
    background-color: #47f;
}

#body.dark {
    background: url('/background.png'); 
    background-size: cover;
    color: #e8e8ef;
}

#body.dark svg {
    fill: #e8e8ef;
}

#body.dark #background-color-fader {
    background-color: #000;
    opacity: 0.7;
}

#body.dark #period {
    background-color: #429;
}

/* ! ----------- ! */
/* ! MAIN STYLES ! */
/* ! ----------- ! */

#background-color-fader {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 1;

    -webkit-transition: background-color 0.5s;

    transition: background-color 0.5s;
}

#light-mode-controls {
    position: absolute;
    bottom: 0;
    left: 0;
    margin: 30px;
    grid-gap: 15px;
    gap: 15px;

    z-index: 2;

    display: flex;
    align-items: center;
}

.mode-select {
    width: 36px;
    height: 36px;
    border-radius: 100%;
}

#dark-mode-select {
    background-color: #429;
}

#dark-mode-select.selected {
    border: 3px solid #e8e8ef;
}

#light-mode-select {
    background-color: #acf;
}

#light-mode-select.selected {
    border: 3px solid #171710;
}

#intro-container {
    z-index: 2;
    display: flex;
    flex-direction: column;
    
    margin-left: 8%;
}

#name {
    font-size: 60px;
    line-height: 60px;

    font-weight: 500;

    -webkit-animation: 0.75s ease-out 0s 1 fadeIn,
        0.75s ease-out 0s 1 fromTop;

            animation: 0.75s ease-out 0s 1 fadeIn,
        0.75s ease-out 0s 1 fromTop;
}

#name #period {
    display: inline-block;
    width: 0.75rem;
    height: 0.75rem;
    border-radius: 0.2rem;
    margin-left: 2px;
}

#pronunciation {
    font-size: 20px;
    line-height: 20px;
    font-weight: 300;
    margin-bottom: 30px;
    opacity: 0;

    -webkit-animation: 0.75s ease-out 0.3s 1 fadeIn,
        0.75s ease-out 0.3s 1 fromBottom;

            animation: 0.75s ease-out 0.3s 1 fadeIn,
        0.75s ease-out 0.3s 1 fromBottom;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

#slogan {
    font-size: 20px;
    opacity: 0;

    -webkit-animation: 0.75s ease-out 0.75s 1 fadeIn,
        0.75s ease-out 0.75s 1 fromBottom;

            animation: 0.75s ease-out 0.75s 1 fadeIn,
        0.75s ease-out 0.75s 1 fromBottom;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

#socials {
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    grid-gap: 30px;
    gap: 30px;
}

/* ! SVG SECTION */

#socials svg {
    width: 36px;
    height: 36px;
}

#socials a {
    opacity: 0;

    -webkit-animation: 0.75s ease-out 0s 1 fadeIn,
        0.75s ease-out 0s 1 fromBottom;

            animation: 0.75s ease-out 0s 1 fadeIn,
        0.75s ease-out 0s 1 fromBottom;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

#socials a:nth-child(1) {
    -webkit-animation-delay: 1s;
            animation-delay: 1s
}

#socials a:nth-child(2) {
    -webkit-animation-delay: 1.1s;
            animation-delay: 1.1s
}

#socials a:nth-child(3) {
    -webkit-animation-delay: 1.2s;
            animation-delay: 1.2s
}

#socials a:nth-child(4) {
    -webkit-animation-delay: 1.3s;
            animation-delay: 1.3s
}

@media only screen and (min-width: 600px) {
    #name {
        font-size: 100px;
        line-height: 100px;
    }

    #slogan {
        font-size: 30px;
    }
}
